/* Toggle Button */
.nav-toggle-btn {
  position: fixed;
  top: 3px;
  left: 3px; /* Adjust the position based on nav bar width */
  z-index: 2000; /* Ensure button is above all other content */
  background-color: #333;
  color: white;
  border: none;
  padding: 10px 15px;
  cursor: pointer;
  border-radius: 5px;
  transition: left 0.3s ease;
  background-color: rgba(0, 0, 0, 0);
  opacity: 50%;
  transition: opacity 0.2s ease-in-out; /* Duration and timing function */
}

.nav-toggle-btn:hover {
  background-color: rgba(0, 0, 0, 0);
  opacity: 100%;
}

.nav.collapsed ~ .nav-toggle-btn {
  left: 20px; /* Move button to the left when nav is collapsed */
}

/* Style the navigation bar */
.nav {
  width: 149px; /* Default width */
  height: 100vh; /* Full height of the viewport */
  position: absolute; /* Absolute positioning to stay on top */
  top: 0;
  left: 0;
  background-color: #1b1b1b; /* Background color */
  display: flex;
  align-items: start;
  justify-content: center;
  transition: width 0.3s ease; /* Smooth transition for width */
  z-index: 1000; /* Ensure it is above other content */
}

/* Collapsed state for nav */
.nav.collapsed {
  width: 60px; /* Collapsed width */
}

/* Style the unordered list inside the navigation */
.nav ul {
  list-style-type: none; /* Remove bullets */
  padding: 0;
  margin: 0;
}

/* Style each list item */
.nav ul li {
  height: 20px;
  background-color: rgba(255, 0, 0, 0);
  border: none;
  width: fit-content;
}

/* Style the links */
.nav ul li a {
  color: white;
  text-decoration: none;
  display: block;
  padding: 10px 10px;
  border-radius: 5px;
  text-align: center;
  font-size: 18px;
  background-color: rgba(64, 64, 64, 0); /* Transparent background */
  transition: background-color 0.17s ease-in-out;
  width: 100%;
  white-space: nowrap; /* Prevent text from wrapping */
  overflow: hidden; /* Hide overflow text */
  
  opacity: 85%;
  text-shadow: 0 0 0px rgba(0, 255, 255, 0.7); /* Glow effect */
  transition: text-shadow 0.2s ease-in-out;
}

/* Hide text in links when nav is collapsed */
.nav.collapsed ul li a {
  font-size: 0; /* Hide text */
}

/* Show icons only when collapsed */
.nav.collapsed ul li a::before {
  font-size: 24px; /* Display icon larger */
  font-size-adjust: inherit;
  margin-right: 0;
}

/* Style the links on hover */
.nav ul li a:hover {
  /* background-color: #2f2f2f;  */
  opacity: 100%;
  text-shadow: 0 0 7px rgb(78, 78, 78);
}

/* Adjust main content padding to avoid overlap with nav */
.main-content {
  padding: 20px;
  transition: margin-left 0.3s ease; /* Smooth transition */
}

/* Expanded state for main content when nav is collapsed */
.main-content.expanded {
  margin-left: 0; /* No margin when nav is collapsed */
}

/* Dark overlay when nav is expanded */
.overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.695); /* Semi-transparent dark background */
  z-index: 900; /* Ensure it's below the nav but above main content */
  display: none; /* Hidden by default */
  transition: opacity 0.3s ease;
  
}

/* Show overlay when nav is expanded */
.nav.collapsed ~ .overlay {
  display: none; /* Hide overlay when nav is collapsed */
}

.nav ~ .overlay {
  display: block; /* Show overlay when nav is expanded */
  opacity: 1;
}


